/*About us block*/
/*general block*/
.about-general h1 {
    text-transform: uppercase;
    margin: 0 0 1.5rem;
}
.logo {
    width: 150px;
    border-radius: 50%;
    margin-bottom: 32px;
}
.icon-link {
    transition: 0.3s;
}
.icon-link:hover {
    color:  cadetblue;
}
.site-title {
    font-size: calc(36px + 3vw);
}
.site-subtitle {
    font-weight: normal;
    text-transform: none;
    font-size: calc(1.5rem + 1vw);
}
.about-general .page-header {
    margin: 48px 0;
    text-align: center;
    border-bottom: none;
}
.box {
    padding: 3rem;
}
.about,
.page-footer {
    border-top: 1px solid;
}
.contact {
    text-align: center;
    margin: 48px 0;
    color: #fff;
    background: #212121;
}
.quote {
    padding: 4.5rem 0;
    text-align: center;
    background:  url(https://unsplash.it/1200/800?image=354) no-repeat;
    background-size:  cover;
    background-attachment: fixed;
}
blockquote {
    color: #fff;
    font-size: calc(1rem + 2vw);
}
blockquote strong {
    display: block;
}
.page-footer {
    text-align: center;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}
address {
    margin-bottom: 1.5rem;
}


/*Team block*/
.team_member {
    width:100%;
    position:relative;
    overflow:hidden;
    height:250px;
}

.info {
    height:100%;
}

.info img {
    width:100%;
    transition: all .4s ease-out .1s;
    -moz-transition: all .4s ease-out .1s;
    -webkit-transition: all .4s ease-out .1s;
}

.info h5 {
    padding: 10px 10px 0 0;
    margin: 0 0 10px 3px;
    line-height: 1em;
    opacity: 1;
    transition: all .2s ease-out .1s;
    -moz-transition: all .2s ease-out .1s;
    -webkit-transition: all .2s ease-out .1s;
    font-size: 1.125em;
}

.info h6 {
    padding:0 10px 0 0;
    font-size:12px;
    margin:0 0 0 3px;
    line-height:1em;
    text-transform:uppercase;
    color:#999;
    opacity:1;
    transition: all .2s ease-out .1s;
    -moz-transition: all .2s ease-out .1s;
    -webkit-transition: all .2s ease-out .1s;
}

.info_reveal {
    height:100%;
    transition: all .3s ease-in 0s;
    -moz-transition: all .3s ease-in 0s;
    -webkit-transition: all .23s ease-in 0s;
    position:absolute;
    width:100%;
    left:0;
    top:-100%;
    background: rgba(19,148,119,0.9);
}

.info_reveal h6 {
    padding: 20px 8px 5px 8px;
    line-height: 1.1em;
    margin-bottom: 20px;
    color: #fff;
    font-size: 1em;
}

.info_reveal p {
    padding:0 8px;
    font-size:15px;
    line-height: 1.2em;
    color: #073a2e;
    font-weight:400;
}

.info_reveal a {
    color: #073a2e;
    word-break: break-word;
    text-decoration:none;
}

.info_reveal a:hover {
    color: #073a2e;
}


.responsive_grid {
    display:block;
    margin:0;
    padding:0;
    list-style:none;
}

.responsive_grid li {
    cursor:pointer;
    width: 16.66667%;
    padding: 0 10px 10px;
    display:block;
    height:auto;
    float:left;
    margin-bottom:10px;
}

.responsive_grid li:hover .info_reveal,
.responsive_grid li:focus .info_reveal{
    left: 0;
    top:0;
    transition: all .3s ease-out .1s;
    -moz-transition: all .3s ease-out .1s;
    -webkit-transition: all .3s ease-out .1s;
}

.responsive_grid li:hover .info img,
.responsive_grid li:focus .info img {
    width:210%;
    margin-left:-50%;
    transition: all .4s ease-in 0s;
    -moz-transition: all .4s ease-in 0s;
    -webkit-transition: all .4s ease-in 0s;
}

.responsive_grid li:hover .info h5,
.responsive_grid li:focus .info h5,
.responsive_grid li:hover .info h6,
.responsive_grid li:focus .info h6 {
    opacity: 0;
    transition: opacity .2s linear 0s;
    -moz-transition: opacity .2s linear 0s;
    -webkit-transition: opacity .2s linear 0s;
}

h5, h6, .info_reveal a {
    font-family: 'Open Sans Condensed', sans-serif;
}